export function inputTerm(evt) {
    // console.log(evt.target.value)
    // let a = yTSearch(evt.target.value)
    // console.log(a)
    return(dispatch)=> {
        // yTSearch(evt.target.value, dispatch)
        dispatch({
            type: 'inputTerm',
            payload: {
              term:evt.target.value
            }
        })
    }
}

export function handleItem(item) {
  return(dispatch)=> {
    dispatch({
      type: 'CHOOSE_ITEM',
      payload: {
        item:item
      }
    })
  }
}

export function closeModal() {
  return(dispatch)=> {
    dispatch({
      type: 'CLOSE_MODAL',
    })
  }
}